import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import albumData from './../data/albums';
import './Library.css';

class Library extends Component {
   constructor(props) {
     super(props);
     this.state = { albums: albumData };
   }

  render() {
    return (
      <section className="library">
         {
           this.state.albums.map( (album, index) =>
             <a className="album-link" href={`./album/${album.slug}`} key={index} >
               <img className="album-art" src={album.albumCover} alt={album.title} />
               <div className="album-info">{album.title}</div>
               <div className="album-info">{album.artist}</div>
               <div id="album-length" className="album-info">{album.songs.length} songs</div>
             </a>
           )
         }
       </section>
    );
  }
}

export default Library;
